.headerWrapper {
    background-color: bisque;
    width: 100%;
    height: 90px;
    grid-column: 1/13;
    display: grid;
    grid-template-columns: 900px 100px;
    grid-template-rows: 90px;
}

.headerImgLogo {
    width: 130px;
    height: 100px;
    float: left;
    padding-left: 10px;
}

.headerText {
    padding-right: 15px;
}